/*
=====================================================================================
Termina normalize
=====================================================================================
empieza globales
=====================================================================================
*/
@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Arima:wght@100;200;300;400;500;600;700&display=swap");
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}
body {
  font-family: "Arima", cursive;
  font-size: 1.6rem;
  min-height: 100%;
}
#root {
  background-color: rgba(0, 0, 0, 0.8);
  min-height: 100vh;
}
.land {
  background: url(../img/landing-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}
.count {
  background: url(../img/bg_Countries.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}
.botones {
  display: flex;
  flex-direction: row-reverse;
  padding: 0 2rem;
  width: 100%;
}
.unShow {
  display: none;
}
.show {
  display: block;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
}

/*
================================================================================
Tipografia por defecto 
================================================================================
*/

h1,h2,h3,h4,h5,h6 {
  font-family: "Libre Baskerville", serif;
  margin: 0 0 calc(4rem / 2) 0;
  color: #ffffff;
  font-weight: 900;
}
h1 {
  font-size: 6rem;
}
h2 {
  font-size: 5rem;
}
h3 {
  font-size: 4rem;
}
p {
  font-size: 2rem;
  color: #f3f4f6;
  line-height: 1.8;
}
.tagline {
  font-family: "Libre Baskerville", serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}
.alerta {
  text-align: center;
  font-size: 2.5rem;
  border-bottom: 2px solid;
  padding: 1rem;
  border-radius: 1.5rem;
}

.alerta.rojo {
  background-color: rgba(100, 0, 0, 0.2);
  color: rgb(255,100,100);
}
.alerta.verde {
  background-color: rgba(rgb(0, 120, 0), 0.2);
  color: rgb(0, 120, 0);
}
.alerta.amarillo {
  background-color: rgba(#ffff00, 0.2);
  color: #ffff00;
}
